@import url(https://fonts.googleapis.com/css2?family=Abel&family=Poppins&display=swap);
:root {
  --white: #fff;
  --blue: #3498db;
  --grey: rgb(32, 32, 32);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
html,
body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
}
#root {
  position: relative;
  width: 100%;
  height: 100%;
}

